// pkgs/libs/src/wait-until.ts
var waitUntil = (condition, timeout) => {
  return new Promise(async (resolve) => {
    if (typeof condition === "function") {
      let tout = null;
      if (timeout) {
        tout = setTimeout(resolve, timeout);
      }
      if (await condition()) {
        clearTimeout(tout);
        resolve();
        return;
      }
      let count = 0;
      const c = setInterval(async () => {
        if (await condition()) {
          if (tout)
            clearTimeout(tout);
          clearInterval(c);
          resolve();
        }
        if (count > 100) {
          clearInterval(c);
        }
      }, 100);
    } else if (typeof condition === "number") {
      setTimeout(() => {
        resolve();
      }, condition);
    }
  });
};

// pkgs/libs/src/db-client.ts
import find from "lodash.find";
var toSnake = (str) => str[0].toLowerCase() + str.slice(1, str.length).replace(/[A-Z]/g, (letter) => `_${letter.toLowerCase()}`);
var isProxy = Symbol("isProxy");
var prepareDBClient = (dbname) => {
  let proxy = new Proxy({}, {
    get(_, name) {
      const w = window;
      let baseUrl = "";
      if (!w.is_dev && w.hostname) {
        baseUrl = `${w.hostname}`;
      }
      const post = async (params) => {
        let url = `${baseUrl}/__data/${toSnake(params.action)}`;
        if (params.table) {
          url += `...${params.table}`;
        }
        const options = {
          method: "POST",
          headers: {
            Accept: "application/json",
            "Content-Type": "text/plain"
          },
          body: JSON.stringify(params)
        };
        const fetching = await fetch(url, options);
        return await fetching.json();
      };
      if (name === "query") {
        return async (q) => {
          if (Array.isArray(q))
            return [];
          w.global = w;
          if (!w.sodium) {
            w.sodium = (await import("sodium-universal")).default;
          }
          if (!w.Buffer) {
            w.Buffer = (await w.loadExt("dev/buffer.js")).buffer.Buffer;
          }
          if (typeof w.secret === "object" && w.secret.data) {
            w.secret = w.secret.data;
          }
          var nonce = w.Buffer.alloc(w.sodium.crypto_secretbox_NONCEBYTES);
          var key = w.Buffer.from(w.secret);
          var message = w.Buffer.from(q);
          var result = w.Buffer.alloc(message.length + w.sodium.crypto_secretbox_MACBYTES);
          w.sodium.randombytes_buf(nonce);
          w.sodium.crypto_secretbox_easy(result, message, nonce, key);
          let url = `${baseUrl}/__data/query`;
          const options = {
            method: "POST",
            headers: {
              Accept: "application/json",
              "Content-Type": "text/plain",
              "x-nonce": nonce.toString("hex")
            },
            body: result
          };
          const res = await fetch(url, options);
          return await res.json();
          return [];
        };
      }
      return new Proxy({}, {
        get(_2, sname) {
          if (sname === isProxy)
            return true;
          return async function(...params) {
            const w2 = window;
            const key = `${dbname}.${name.toString()}`;
            if (sname === "definition") {
              if (!w2.tableDefinitions) {
                w2.tableDefinitions = {};
              }
              if (!window.is_dev && localStorage[`dbdef-${name.toString()}`]) {
                w2.tableDefinitions[key] = JSON.parse(localStorage[`dbdef-${name.toString()}`]);
              }
              if (w2.tableDefinitions[key]) {
                await waitUntil(() => w2.tableDefinitions[key] !== "loading");
                return w2.tableDefinitions[key];
              }
              w2.tableDefinitions[key] = "loading";
            }
            const result = await post({
              table: name,
              db: dbname,
              action: sname,
              params
            });
            if (result && result.status === "failed" && result.reason) {
              throw new Error(`db.${name.toString()}.${sname.toString()}() ${result.reason}`);
            }
            if (sname === "definition") {
              for (let [k, val] of Object.entries(result.columns)) {
                val.type = val.type.toLowerCase();
              }
              const pk = find(result.columns, { pk: true });
              result.pk = pk.name;
              w2.tableDefinitions[key] = result;
              localStorage[`dbdef-${name.toString()}`] = JSON.stringify(result);
            }
            return result;
          };
        }
      });
    }
  });
  return proxy;
};

// pkgs/libs/src/ellapsed-time.ts
var ellapsedTime = (fromMs) => {
  const t1 = Math.round(new Date().getTime() - fromMs) / 1e3;
  return Math.round(t1 * 10) / 10;
};

// pkgs/libs/src/match-route.ts
import { match } from "path-to-regexp";
var matchRoute = (path, pattern) => {
  let pat = pattern.replace(/\./gi, "/").replace(/\[([^\]]+)\]/g, ":$1?").replace("*", "(.*)");
  if (pat.indexOf("/") !== 0) {
    pat = "/" + pat;
  }
  if (pat.indexOf("/:/") === 0) {
    pat = pat.substr(2);
  }
  const trymatch = (ptrn) => {
    const regex = match(ptrn, { decode: decodeURIComponent });
    const params2 = regex(path);
    return params2;
  };
  const run = (pat2) => {
    let params = trymatch(pat2);
    if (!params) {
      const parr = pat2.split("/");
      if (parr[parr.length - 1] === "index") {
        parr.pop();
        pat2 = parr.join("/");
        params = trymatch(pat2);
      }
    }
    if (!!params) {
      if (params.params) {
        return params.params;
      }
      return params;
    }
    return null;
  };
  const patarr = pat.split("/:");
  if (patarr.length > 1) {
    let testrun = [];
    let first = "";
    if (patarr[0].indexOf("/") === 0) {
      first = patarr.shift() || "";
      testrun.push(`${first}`);
    }
    let url = `${first}`;
    for (let i of patarr) {
      if (!!i.trim()) {
        url += `/:${i}`;
        testrun.push(url);
      }
    }
    testrun = testrun.reverse();
    for (let p of testrun) {
      const res = run(p);
      if (res) {
        return res;
      }
    }
  } else {
    return run(pat);
  }
};

// pkgs/libs/src/use-window.ts
var useWindow = () => {
  let win = null;
  if (typeof global !== "undefined" && typeof window === "undefined") {
    if (global.generateIndexHTML && global.generateIndexHTML.window) {
      win = global.generateIndexHTML.window;
    } else if (global.requestContext) {
      win = global.requestContext.get("window");
    }
  } else {
    win = window;
  }
  return {
    window: win,
    location: win.location,
    document: win.document
  };
};

// pkgs/libs/src/index.tsx
var db = prepareDBClient("main");
export {
  db,
  ellapsedTime,
  matchRoute,
  useWindow,
  waitUntil
};
